/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'roboto_condensedregular', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
  background: #e9eae2; }

.header {
  display: grid;
  grid-template-columns: repeat(16, 1fr); }

.header__left {
  grid-column: span 11;
  padding-left: 15.9%;
  padding-top: 4.8%;
  padding-bottom: 8.7%;
  background: url("/img/image1.png") 100% center no-repeat;
  background-size: 100%; }
  @media (min-resolution: 2dppx), (min-width: 1500px) {
    .header__left {
      background-image: url("/img/image1-2x.png"); } }

.header__right {
  grid-column: span 5; }

.header__logo {
  display: block;
  max-width: 100%; }

.header__slogan {
  font-size: 18px;
  color: #333333;
  text-transform: uppercase;
  line-height: 1;
  margin: 0;
  margin-top: 5px; }
  @media (min-width: 1500px) {
    .header__slogan {
      font-size: calc(112.5% + (36 - 18) * (100vw - 1500px) / (3000 - 1500)); } }

.header__socials {
  margin-top: 60px;
  margin-left: 15px;
  margin-bottom: 15px; }

.header__right {
  background-color: #7bcbc1;
  position: relative;
  padding-top: 10.7%; }
  .header__right::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background-image: url('data:image/svg+xml;utf8, <svg xmlns="http://www.w3.org/2000/svg" width="100%" height="100%"><line x1="0" y1="100%" x2="100%" y2="0" stroke="rgb(255,255,255)" stroke-width="1"/></svg>'); }

.header__address {
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.077;
  position: absolute;
  left: 12.9%;
  bottom: 36.5%;
  margin: 0; }
  @media (min-width: 1500px) {
    .header__address {
      font-size: calc(81.25% + (26 - 13) * (100vw - 1500px) / (3000 - 1500)); } }

.header__contacts {
  font-size: 13px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.077;
  left: 29%;
  bottom: 20%;
  position: absolute;
  margin: 0; }
  @media (min-width: 1500px) {
    .header__contacts {
      font-size: calc(81.25% + (26 - 13) * (100vw - 1500px) / (3000 - 1500)); } }

.header__tel, .header__mail {
  color: #fff;
  line-height: 1.077;
  text-decoration: none;
  display: inline-block;
  transition-property: color;
  transition-duration: 500ms; }
  .header__tel:hover, .header__mail:hover {
    color: #f8c255; }

@media (max-width: 980px) {
  .header__left {
    grid-column: span 9; }
  .header__right {
    grid-column: span 7; } }

@media (max-width: 560px) {
  .header {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr; }
  .header__left {
    grid-column: span 1;
    grid-row: 2 / 3; }
  .header__right {
    grid-column: span 1;
    grid-row: 1 / 2; }
  .header__address {
    position: static;
    margin-left: 10%;
    margin-top: 10%; }
  .header__contacts {
    position: static;
    margin-left: 35%;
    margin-top: 20%;
    margin-bottom: 20%; } }

.menu {
  position: absolute;
  right: 11%;
  top: 50px; }

.menu__hamburger {
  width: 119px;
  height: 114px; }

.menu__list {
  background: #f8c255;
  list-style: none;
  margin: 0;
  padding: 0;
  align-items: center;
  flex-wrap: wrap;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  min-width: 725px;
  padding-right: 119px;
  padding-left: 25px;
  box-sizing: border-box;
  display: none;
  z-index: -9999; }

.menu__list--active {
  display: flex;
  z-index: 1;
  animation-duration: 500ms;
  animation-name: menu; }

.menu__link {
  font-size: 14px;
  color: #fff;
  text-transform: uppercase;
  line-height: 1.786;
  text-decoration: none;
  padding: 30px;
  transition-duration: 500ms;
  transition-property: color; }
  @media (min-width: 1500px) {
    .menu__link {
      font-size: calc(87.5% + (28 - 14) * (100vw - 1500px) / (3000 - 1500)); } }
  .menu__link:hover {
    color: #000; }

@keyframes menu {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@media (min-width: 1500px) {
  .menu__list {
    min-width: 1100px; } }

@media (max-width: 980px) {
  .menu {
    right: 30px;
    top: 30px; } }

@media (max-width: 768px) {
  .menu {
    height: 100%; }
  .menu__hamburger {
    width: 70px;
    height: 70px; }
  .menu__list--active {
    display: block;
    min-width: auto;
    padding-right: 70px;
    padding-top: 15px;
    overflow-y: auto; }
  .menu__link {
    display: block;
    padding: 7px 80px 7px 0; } }

.footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }

.footer__left {
  margin-left: 15.3%;
  margin-top: 20%; }

.footer__right {
  margin-right: 22%;
  margin-top: 20%; }

.footer__map {
  grid-column: span 1; }

.footer__info {
  grid-column: span 1; }

.footer__logo {
  display: block;
  max-width: 100%; }

.footer__slogan {
  font-size: 18px;
  color: #333333;
  text-transform: uppercase;
  line-height: 1;
  margin: 0;
  margin-top: 5px; }
  @media (min-width: 1500px) {
    .footer__slogan {
      font-size: calc(112.5% + (36 - 18) * (100vw - 1500px) / (3000 - 1500)); } }

.footer__socials {
  margin-top: 60px;
  margin-left: 15px;
  margin-bottom: 15px; }

.footer__address {
  font-size: 13px;
  color: #333333;
  text-transform: uppercase;
  line-height: 1.077;
  margin: 10% 0; }
  @media (min-width: 1500px) {
    .footer__address {
      font-size: calc(81.25% + (26 - 13) * (100vw - 1500px) / (3000 - 1500)); } }

.footer__contacts {
  font-size: 13px;
  color: #333333;
  text-transform: uppercase;
  line-height: 1.077;
  margin: 10% 0; }
  @media (min-width: 1500px) {
    .footer__contacts {
      font-size: calc(81.25% + (26 - 13) * (100vw - 1500px) / (3000 - 1500)); } }

.footer__tel, .footer__mail {
  color: #333333;
  line-height: 1.077;
  text-decoration: none;
  display: inline-block;
  transition-property: color;
  transition-duration: 500ms; }
  .footer__tel:hover, .footer__mail:hover {
    color: #f8c255; }

.footer__info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.footer__copy {
  font-size: 12px;
  color: #333333;
  line-height: 1.8;
  text-align: center;
  flex-basis: 100%;
  margin: 9% 0 7% 0; }
  @media (min-width: 1500px) {
    .footer__copy {
      font-size: calc(75% + (24 - 12) * (100vw - 1500px) / (3000 - 1500)); } }

@media (max-width: 1500px) {
  .footer__right {
    margin-right: 2%; } }

@media (max-width: 768px) {
  .footer {
    grid-template-columns: 1fr; }
  .footer__map {
    padding-bottom: 75%; }
  .footer__left {
    margin-left: 5%; }
  .footer__right {
    margin-right: 5%; } }

@media (max-width: 560px) {
  .footer__info {
    display: block; }
  .footer__left {
    text-align: center;
    margin-right: 5%;
    margin-top: 10%; }
  .footer__right {
    text-align: center;
    margin-left: 5%;
    margin-top: 5%; }
  .footer__logo {
    margin: 0 auto; }
  .footer__socials {
    margin-left: 0;
    justify-content: center; } }

